import React from 'react';
import { Typography, AppBar, CssBaseline, Toolbar, Container, Box, Button, ToggleButtonGroup, ToggleButton, Dialog, DialogContent, DialogContentText, DialogTitle } from '@mui/material';
import WbSunnyIcon from '@mui/icons-material/WbSunny';
import { createTheme } from "@mui/material/styles";


const App = () => {
    const [alignment, setAlignment] = React.useState();
    const [open, setOpen] = React.useState(false);

    const handleChange = (event, newAlignment) => {
        setAlignment(newAlignment);
    };

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
        setAlignment();
    };
    return (
        <>
            <CssBaseline />
            <AppBar position='relative'>
                <Toolbar>
                    <WbSunnyIcon style={{ marginRight: '20px' }} />
                    <Typography variant='h6'>
                        Sunshine Clinic
                    </Typography>
                </Toolbar>
            </AppBar>
            <Box sx={{ paddingTop: 2 }} />
            <main>
                <div>
                    <Container maxWidth='md'>
                        <Typography variant='h6' align='left' color='textPrimary' gutterBottom gutterTop>
                            What is your biological sex?
                        </Typography>
                    </Container>
                    <Box sx={{ paddingTop: 2 }} />
                    <Container maxWidth='md'>
                        <Box component="span" m={1} display="flex" justifyContent="center" alignItems="center">

                            <ToggleButtonGroup
                                fullWidth="true"
                                orientation="vertical"
                                color="primary"
                                value={alignment}
                                exclusive
                                onChange={handleChange}
                            >
                                <ToggleButton color='secondary' value="0">Male</ToggleButton>
                                <ToggleButton color='error' value="1">Female</ToggleButton>
                                <ToggleButton color='success' value="2">Other Options</ToggleButton>

                            </ToggleButtonGroup>
                        </Box>
                    </Container>
                    <Box sx={{ paddingTop: 2 }} />
                    <Container maxWidth='md'>
                        <Box component="span" m={1} display="flex" justifyContent="space-between" alignItems="center">
                            <Button onClick={handleClickOpen} size="large" color="primary" variant="contained" >Back</Button>
                            <Button onClick={handleClickOpen} size="large" color="success" variant="contained" disabled={!alignment}>Continue</Button>
                        </Box>
                    </Container>

                    <Dialog
                        open={open}
                        onClose={handleClose}
                        aria-labelledby="alert-dialog-title"
                        aria-describedby="alert-dialog-description"
                    >
                        <DialogTitle id="alert-dialog-title">
                            {"This is just a demo!"}
                        </DialogTitle>
                        <DialogContent>
                            <DialogContentText id="alert-dialog-description">
                                This is just a demo of what material ui can accomplish in less than 3 hours from someone who has never used material ui before. This really showcases how powerful the library is. 
                            </DialogContentText>
                        </DialogContent>
                    </Dialog>


                </div>
            </main>
        </>
    );
}

export default App;